@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap");

* {
  font-family: "Ubuntu", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --blue: #287bff;
  --white: #fff;
  --gray: #f5f5f5;
  --black1: #222;
  --black2: #999;
}

body {
  min-height: 100vh;
  overflow: auto;
}

body::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.container {
  position: relative;
  width: 100%;
}

/* =============== Navigation ================ */
.navigation {
  position: fixed;
  width: 80px;
  height: 100%;
  background: var(--blue);
  border-left: 10px solid var(--blue);
  transition: 0.5s;
  overflow: hidden;
}

.navigation.active {
  width: 300px;
}

.navigation ul {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.navigation ul li {
  position: relative;
  width: 100%;
  list-style: none;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

.navigation ul li:hover,
.navigation ul li.hovered {
  background-color: var(--white);
}

.navigation ul li:nth-child(1) {
  margin-bottom: 40px;
  pointer-events: none;
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  display: flex;
  text-decoration: none;
  color: var(--white);
}

.navigation ul li:hover a,
.navigation ul li.hovered a {
  color: var(--blue);
}

.navigation ul li a .icon {
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  line-height: 75px;
  text-align: center;
}

.navigation ul li a .icon ion-icon {
  font-size: 1.75rem;
}

.navigation ul li a .title {
  position: relative;
  display: block;
  padding: 0 10px;
  height: 60px;
  line-height: 60px;
  text-align: start;
  white-space: nowrap;
}

/* --------- curve ---------- */
.navigation ul li:hover a::before,
.navigation ul li.hovered a::before {
  content: "";
  position: absolute;
  right: 0;
  top: -50px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border-radius: 50%;
  box-shadow: 35px 35px 0 10px var(--white);
  pointer-events: none;
}

.navigation ul li:hover a::after,
.navigation ul li.hovered a::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -50px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border-radius: 50%;
  box-shadow: 35px -35px 0 10px var(--white);
  pointer-events: none;
}

/* ===================== Main ===================== */
.main {
  position: absolute;
  width: calc(100% - 80px);
  left: 80px;
  min-height: 100vh;
  background: var(--white);
  transition: 0.5s;
}

.main.active {
  width: calc(100% - 300px);
  left: 300px;
}

.topbar {
  position: sticky;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.1);
  padding: 20px 10px;
}

.toggle {
  position: relative;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  cursor: pointer;
}

.search {
  position: relative;
  width: 400px;
  margin: 0 15px;
}

.search label {
  position: relative;
  width: 100%;
}

.search label input {
  width: 100%;
  height: 35px;
  border-radius: 40px;
  padding: 10px 20px;
  padding-left: 20px;
  font-size: 13px;
  cursor: pointer;
  outline: none;
  background-color: #eceff1;
  border: 0px solid var(--black2);
}

.rightnav {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: #000000a1;
}

.notif {
  position: relative;
  padding: 18px;
  margin-top: 7px;
  cursor: pointer;
}

.notif::after {
  content: "";
  position: absolute;
  top: 18px;
  right: 21px;
  width: 3px;
  height: 3px;
  background-color: rgb(255, 0, 0);
  color: white;
  font-size: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 3px;
}

.user {
  position: relative;
  margin-right: 20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}

.user img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


/* ======================= Cards ====================== */
.cardBox {
  position: relative;
  width: 100%;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.cardBox .card {
  position: relative;
  background: var(--white);
  padding: 25px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
}

.cardBox .card .numbers {
  position: relative;
  font-weight: 500;
  font-size: 2rem;
  color: var(--blue);
}

.cardBox .card .cardName {
  color: var(--black2);
  font-size: 1.1rem;
  margin-top: 5px;
}

.cardBox .card .iconBx {
  font-size: 3rem;
  color: var(--black2);
}

.cardBox .card:hover {
  background: var(--blue);
  transition: 0.3s;
}

.cardBox .card:hover .numbers,
.cardBox .card:hover .cardName,
.cardBox .card:hover .iconBx {
  color: var(--white);
  transition: 0.3s;
}

/* ================== Order Details List ============== */
.details {
  position: relative;
  width: 100%;
  padding: 20px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 20px;
  /* margin-top: 10px; */
}

.details .recentOrders {
  position: relative;
  display: block;
  min-height: 500px;
  background: var(--white);
  padding: 20px;
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
}

.details .cardHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0;
  padding-bottom: 10px;
}

.cardHeader h2 {
  font-weight: 600;
  color: var(--blue);
}

.cardHeader .btn {
  position: relative;
  padding: 5px 10px;
  font-size: 13px;
  background: var(--blue);
  text-decoration: none;
  color: var(--white);
  border-radius: 6px;
}

.details table {
  width: 100%;
  border-collapse: collapse;
}

.details table thead td {
  font-weight: 600;
}

.details table tbody td {
  font-size: 14px;
}

.details .recentOrders table tr {
  color: var(--black1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.details .recentOrders table tr:last-child {
  border-bottom: none;
}

.details .recentOrders table tbody tr:hover {
  background: var(--blue);
  color: var(--white);
  transition: 0.2s;
}

.details .recentOrders table tr td {
  padding: 15px;
}

.details .recentOrders table tr td:last-child {
  text-align: center;
}

.details .recentOrders table tr td:nth-child(2) {
  text-align: end;
  padding-right: 50px;

}

.details .recentOrders table tr td:nth-child(3) {
  text-align: center;
}

.status.delivered {
  padding: 5px 5px;
  color: #048519;
  background-color: #deffe3;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 600;
}

.status.pending {
  padding: 5px 10px;
  color: #dfbd00;
  background-color: #f9fab3;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 600;
}

.status.return {
  padding: 5px 10px;
  color: #ff5151;
  background-color: #fbeaea;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 600;
}

.status.inProgress {
  padding: 5px 5px;
  color: #0095da;
  background-color: #d3f1ff;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 600;
}


.recentCustomers {
  position: relative;
  display: grid;
  min-height: 500px;
  padding: 20px;
  background: var(--white);
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
}

.recentCustomers .imgBx {
  position: relative;
  width: 35px;
  height: 35px;
  border-radius: 50px;
  overflow: hidden;
}

.recentCustomers .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.recentCustomers table tr td {
  padding: 12px 10px;
}

.recentCustomers table tr td h4 {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2rem;
}

.recentCustomers table tr td h4 span {
  font-size: 14px;
  color: var(--black2);
}

.recentCustomers table tr:hover {
  background: var(--blue);
  color: var(--white);
  transition: 0.1s;
}

.recentCustomers table tr:hover td h4 span {
  color: var(--white);
}

.notif-popup {
  display: none;
  position: absolute;
  top: 50px;
  right: 70px;
  background-color: rgb(255, 255, 255);
  border: 1px solid #ccc;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  width: 350px;
  padding: 5px;
  z-index: 1000;
  overflow: auto;
}

.notif-popup::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.notif-popup.active {
  display: block;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.notification-header h2 {
  font-size: 15px;
  margin: 0;
  font-weight: 600;
}

.notification-header ion-icon {
  font-size: 18px;
  cursor: pointer;
}

.notification-list {
  padding: 10px;
  max-height: 300px;
  overflow-y: auto;
}

.notification-list::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.notification-item {
  display: flex;
  align-items: start;
  padding: 10px;
  border-bottom: 1px solid #f1f1f1;
  cursor: pointer;
}

.notification-item:hover {
  background-color: #f1f1f1c0;
  transition: 0.1s;
  border-radius: 20px;
}

.notification-item:last-child {
  border-bottom: none;
}

.avatar {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-right: 10px;
}

.icon-check {
  color: green;
  font-size: 24px;
  margin-right: 10px;
  display: flex;
  align-items: center;
}

.notification-content {
  flex: 1;
}

.notification-content p {
  margin: 0 0 5px;
  font-size: 14px;
  line-height: 1.5;
}

.notification-content p strong {
  font-weight: 600;
}

.notification-content p span {
  color: #007BFF;
}

.notification-content a {
  color: #007BFF;
  font-size: 14px;
  text-decoration: none;
}

.notification-content small {
  font-size: 12px;
  color: #999;
}



/* ====================== Responsive Design ========================== */
@media (max-width: 991px) {
  .navigation {
    left: -300px;
  }

  .navigation.active {
    width: 300px;
    left: 0;
  }

  .main {
    width: 100%;
    left: 0;
  }

  .main.active {
    left: 300px;
  }

  .cardBox {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .details {
    grid-template-columns: 1fr;
  }

  .recentOrders {
    overflow-x: auto;
  }

  .status.inProgress {
    white-space: nowrap;
  }
}

@media (max-width: 480px) {
  .cardBox {
    grid-template-columns: repeat(1, 1fr);
  }

  .cardHeader h2 {
    font-size: 20px;
  }

  .user {
    min-width: 40px;
  }

  .navigation {
    width: 100%;
    left: -100%;
    z-index: 1000;
  }

  .navigation.active {
    width: 100%;
    left: 0;
  }

  .toggle {
    z-index: 10001;
  }

  .main.active .toggle {
    color: #fff;
    position: fixed;
    right: 0;
    left: initial;
  }
}


